
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import XImg from 'vux/src/components/x-img/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'


export default {
name: "Attendance",
directives: {
TransferDom
},
components: {
XHeader,
XButton,
Group,
Cell,
XImg,
Badge,
Icon,
InlineCalendar,
Popup,
PopupHeader
},
created(){
this.setTimeShow()
},
mounted() {
this.setMap()
},
data() {
return {
showCalender:false,
zhende: true,


buttonAble:'',
nowDate:dateFormat(new Date(), "YYYY-MM-DD"),
nowTime:dateFormat(new Date(), "HH:mm:ss"),
interVal: '',
distance:Infinity,
inArea:false,                    //进入考勤范围
morningSign: true,              //早考勤
morningLate: false,              //早迟到
eveningSign: false,              //晚考勤
eveningLate: false,              //晚早退
groupInfo:{
lat:'',
lng:'',

},
formData:{

}
};
},

methods: {
goto(path){
this.$router.push({
path:path
})
},
dateViewChange(a,b){
},
getDate(date){
this.showCalender = false;
if(date){
console.log(date)
this.nowDate = date;
}
},
async setMap() {
try{
let group = await this.getSignGroup()
let position = await this.getLocation()
}catch(err){
console.log(err)
}

if (position) {
this.formData.Province = position.address.province;
this.formData.City = position.address.city;
this.formData.District = position.address.district;
this.formData.Street =
position.address.street + position.address.streetNum;
this.formData.Address = position.addresses;
this.formData.MapLng = position.coords.longitude;
this.formData.MapLat = position.coords.latitude;
}

let map = new BMap.Map("container");
let point = new BMap.Point(this.formData.MapLng, this.formData.MapLat);
let marker = new BMap.Marker(point); 
let circle = new BMap.Circle(point, 500, {
strokeColor: "red",
strokeWeight: 1,
strokeOpacity: 0.3
}); //创建圆
map.addOverlay(marker); 
map.addOverlay(circle);
map.centerAndZoom(point, 15);
},
getSignGroup(){
let _url = "/Work/GetMyCrmWorkSigninList?createUserId=204"
return this.getAxios(_url).then(res=>{
return res.Data
})
},
getLocation() {
return new Promise((resolve,reject)=>{
if (window.plus) {
plus.geolocation.getCurrentPosition(
res => {
resolve(res);
},
function (e) {
alert("定位失败，请检查网络");
reject(JSON.stringify(e));
},
{
geocode: true,
coordsType: "bd09ll",
provider: "baidu"
}
);
}
})


},
reLocation() {
this.getLocation();
},

getPicSrc(id) {
return `${apiPic}/dXNlci9sb2dv_${id}_35x35.png`;
},


setTimeShow(){
this.interVal = setInterval(()=>{
this.nowTime = dateFormat(new Date(), "HH:mm:ss")
}, 1000)
}
}
};



